module.exports = "Cette page reprend les différents termes de vocabulaire ayant trait à notre projet de manière à ce que leur orthographe et sens soient bien compris. Elle reprend également les termes à éviter.\n\n```image\nsrc: book_lover.svg\nspan: 3\nplain: true\n```\n\nIl est important que les communications utilisent un langage partagé de manière à faciliter la compréhension par tout un chacun.\n\n```hint|directive\nN’hésitez pas à rediriger les nouveaux Chouettos sur cette page et à l’enrichir afin que nous ayons tous et toutes un **langage commun**.\n_Pour toute suggestion, veuillez contacter le Groupe Communication._\n```\n\n## Termes employés à La Chouette Coop\n\n```hint\nMerci de garder cette liste triée par **ordre alphabétique**.\n```\n\n```table\nrows:\n  - Terme: '**Chouettos**'\n    Définition: |\n      _nom propre._ Un coopérateur ou une coopéatrice ayant adhéré à l’association « Les amis de La Chouette Coop ».\n      _Bon à savoir :_ le terme « Chouettos » est un terme épicène. C’est également un nom propre, et doit commencer par une majuscule.\n      _synonyme : Coopérateur_\n  - Terme: '**Coopérateur**'\n    Définition: |\n      _n._ Une personne physique membre du projet. Un coopérateur doit faire 3h de travail par mois au sein du supermarché afin de pouvoir y effectuer ses courses.\n      _synonyme : Chouettos (à privilégier ;-))_\n  - Terme: '**Lab (le)**'\n    Définition: |\n      _n._  Le Lab (diminutif de « Laboratoire ») est le mini supermarché expérimental  qui préfigure le futur supermarché de La Chouette Coop.\n      _En savoir plus :_ voir [la page dédiée sur l’espace membres](https://espace-membres.lachouettecoop.fr/page/le-lab).\n  - Terme: |\n      **La Chouette Coop**\n      _abbr : LCC_\n    Définition: |\n      _n._ C’est le nom de notre projet de supermarché coopératif !\n      _La bonne orthographe :_ chaque mot doit commencer par une majuscule ainsi les orthographes suivantes sont incorrectes « ~~la~~ Chouette Coop » ou « La ~~chouette coop~~ ».\n  - Terme: '**TAF**'\n    Définition: |\n      _n._ Le TAF (« Temps À Faire ») est le moyen pour les Chouettos de remplir leur engagement de participer régulièrement au projet.\n      Les TAF peuvent s’effectuer au Lab ou dans les groupes de travail.\n      _Bon à savoir :_ le terme TAF est invariable, on écrit donc « les TAF » (et non ~~« les TAFs »~~)\n      _En savoir plus :_ voir [la page dédiée sur l’espace membres](https://espace-membres.lachouettecoop.fr/page/tafs).\n```\n\n## Termes à éviter\n\n```hint\nMerci de garder cette liste triée par **ordre alphabétique**.\n```\n\n```table\nrows:\n  - Terme: '**Client**'\n    Raison: |\n      La Chouette Coop est un projet coopératif. Aussi nous sommes tous et toutes à la fois clients et employés.\n      **Utiliser plutôt :** le terme « Chouettos » (ou « coopérateur / coopératrice »)\n  - Terme: '**Employé**'\n    Raison: |\n      Aucun contrat de travail ne lie un Chouettos à La Chouette Coop, aussi il est important de **bannir tout terme lié à une activité salariée pour désigner les Chouettos**.\n      _Voir aussi :_ « Client »\n  - Terme: |\n      **Supermarché**\n      (au sujet du Lab)\n    Raison: |\n      Le Lab n’est qu’une expérimentation à petite échelle de ce que sera le supermarché final.\n      De par son implantation, son activité et notre structure juridique actuelle ce n’est légalement pas un supermarché !\n      _En revanche_ on peut utiliser le terme de supermarché pour désigner le futur supermarché coopératif qui est le but de La Chouette Coop.\n      **Utiliser plutôt :** le terme « Lab »\n```\n"

